import React from 'react';

class AddUser extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          userName: '',
          errorMsg: ''
        };
      }
      handleChange = event => {
        this.setState({ userName: event.target.value });
      };
    
      handleSubmit = () => {
        const { userName } = this.state;
        if (userName.length === 0) {
          this.setState({ errorMsg: 'Username is required.' });
        } else if (userName.length < 3) {
          this.setState({ errorMsg: 'Minimum 3 characters required.' });
        } else {
          this.props.onSubmit(userName);
          this.setState({ userName: '', errorMsg: '' });
        }
      };
    
    render(){
        const { userName, errorMsg } = this.state;
        return (
            <div className="ck-add-user">
              <div className="ck-user-container">
              <div className="ck-user-box">
                <input type="text" value={userName} onChange={this.handleChange} placeholder="Enter Your Username"/>
                <button onClick={this.handleSubmit}>GO</button>
                <br/>
                <p className="ck-error">{errorMsg}</p>
                </div>
              </div>
            </div>
          );
    }
}

export default AddUser;
