import firebase from 'firebase/app';
import 'firebase/database';

import firebaseConfig from '../config/firebase.config';

export const firebaseSessionPath = 'live-sessions';

window['firebase'] = firebase;

export const initFirebase = () => {
  firebase.initializeApp(firebaseConfig);
};

export const getFirebaseRef = () => {
  const hash = window.location.hash.replace(/#/g, '');
  let ref = firebase.database().ref(firebaseSessionPath);
  if (hash) {
    ref = ref.child(hash);
  } else {
    ref = ref.push(); // generate unique location.
    window.location = window.location + '#' + ref.key; // add it as a hash to the URL.
  }
  return ref;
};

export const addUserName = (refKey, userId, userName) => {
  // save username for future use localstorega 
  saveUserName(refKey, userName);
  return firebase
    .database()
    .ref(`${firebaseSessionPath}/${refKey}/users/${userId}/userName`)
    .set(userName);
};
export const confirmBeforeLeave = (refKey, userId) => {
  window.onbeforeunload = function() {
    return 'Do you want to leave?';
  };
  window.onunload=function() {
    removeUser(refKey, userId);
  }
};
export const removeUser = (refKey, userId) => {
  return firebase
    .database()
    .ref(`${firebaseSessionPath}/${refKey}/users/${userId}`)
    .remove()
};
export const updateEditorMode = (refKey, mode) => {
  return firebase
    .database()
    .ref(`${firebaseSessionPath}/${refKey}/mode`)
    .set(mode);
};

export const offListening = (firebaseSessionKey, key) => {
  return firebase
    .database()
    .ref(`${firebaseSessionPath}/${firebaseSessionKey}/${key}`)
    .off();
};

export const getUserName = (refKey) =>{
  return localStorage.getItem(refKey) || null;
}

const saveUserName = ( refKey, userName) =>{
  localStorage.setItem([refKey], userName);
}
