import React from 'react';
import firebase from 'firebase/app';

import Header from './Header';
import Editor from './Editor';
import Sidebase from './Sidebar';
import AddUser from './AddUser';

import { editorConfig } from '../config/app.config';
// import Footer from './Footer';

import {
  firebaseSessionPath,
  getFirebaseRef,
  updateEditorMode,
  offListening,
  addUserName,
  removeUser,
  confirmBeforeLeave,
  getUserName
} from '../services/firebase';

import './index.css';

export default class Example extends React.Component {
  constructor(props) {
    super(props);
    // Get Firebase Database reference.
    const firebaseRef = getFirebaseRef();
    this.state = {
      selectedMode: editorConfig.language,
      selectedTheme: editorConfig.theme,
      firebaseSessionKey: firebaseRef.key,
      firebaseRef,
      userId: Math.floor(Math.random() * 9999999999).toString(),
      userName: '',
      users: [],
      openSidebar : false,
    };
  }
  initApp = userName => {
    this.changeMode(this.state.selectedMode);
    this.watchEditorModeChangesIntoFirebase();
    this.watchEditorUserChnagesIntoFirebase();
    confirmBeforeLeave(this.state.firebaseSessionKey, this.state.userId);
  };

  changeMode = mode => {
    this.setState({ selectedMode: mode });
    updateEditorMode(this.state.firebaseSessionKey, mode);
  };

  handleUserNameSubmit = username => {
    const { firebaseSessionKey, userId } = this.state;
    addUserName(firebaseSessionKey, userId, username);
    this.setState({ userName: username });
    this.initApp();
  };

  toggleSidebar = ()=>{
    this.setState({ openSidebar : !this.state.openSidebar });
  }
  
  watchEditorModeChangesIntoFirebase = () => {
    firebase
      .database()
      .ref(`${firebaseSessionPath}/${this.state.firebaseSessionKey}/mode`)
      .on('value', snapshot => {
        const updateMode = snapshot.val();
        updateMode && this.setState({ selectedMode: updateMode });
      });
  };

  watchEditorUserChnagesIntoFirebase = () => {
    firebase
      .database()
      .ref(`${firebaseSessionPath}/${this.state.firebaseSessionKey}/users`)
      .on('value', snapshot => {
        const updatedUsers = snapshot.val();
        updatedUsers &&
          this.setState({
            users: Object.keys(updatedUsers).map((key, index) => ({
              userId: key,
              color: updatedUsers[key].color || '#fff',
              userName: updatedUsers[key].userName || `User ${index + 1}`
            }))
          });
      });
  };
  componentDidMount(){
    const userName = getUserName(this.state.firebaseSessionKey);
    userName && this.handleUserNameSubmit(userName);
  }
  componentWillUnmount() {
    const { firebaseSessionKey, userId } = this.state;
    offListening(firebaseSessionKey, 'mode');
    offListening(firebaseSessionKey, 'users');
    removeUser(firebaseSessionKey, userId);
  }

  render() {
    const { width = '500px', height = '400px' } = this.props;
    const { selectedMode, selectedTheme, firebaseRef, userName, userId, users, openSidebar } = this.state;
    const showEditor = userName.length > 0;
    return (
      <div style={{ width: width, height: height }}>
        <div className={openSidebar ? 'ck-main' : 'ck-main ck-short'}>
          <div className="ck-toolbar py-1">
            <Header selectedMode={selectedMode} changeMode={this.changeMode} show={showEditor} />
          </div>
          <div className="ck-directory ">
            <Sidebase users={users} userId={userId} openSidebar={openSidebar} toggleSidebar={this.toggleSidebar} />
          </div>
          <div className="ck-editor ">
            {showEditor ? (
              <Editor mode={selectedMode} selectedTheme={selectedTheme} userId={userId} firebaseRef={firebaseRef} />
            ) : (
              <AddUser onSubmit={this.handleUserNameSubmit} />
            )}
          </div>
          <div className="ck-footer " />
        </div>
      </div>
    );
  }
}
