import React from 'react';

const Sidebar = ({ users, userId, openSidebar,toggleSidebar }) => {
  return (
    <ul className="ck-user-group ck-sidebar">
    <li className="ck-user-list ck-toggle" onClick={toggleSidebar} >
            <div className="ck-user-circle icon" > {openSidebar ? '	‹' : '›' }	 </div>
            <div className="ck-username"><span>&nbsp;Users List</span></div>
      </li>
      {users.map(user => {
        return (
          <li key={user.userId} className="ck-user-list">
            <div className="ck-user-circle" style={{ backgroundColor: user.color }} >{user.userName[0].toUpperCase()}</div>
            <div className="ck-username"><span className={userId===user.userId  ? "ck-me" : ""}> &nbsp;{user.userName} {userId===user.userId ? ` | Me` : ''}</span></div>
          </li>
        );
      })}
    </ul>
  );
};

export default Sidebar;
